* {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-style: normal;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  color: #212121;
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
    margin: 0 auto;
  }
}/*# sourceMappingURL=main.css.map */